.return-btn:hover path{
    fill:red
}
.main-contact.thanks .section{
    margin-bottom: 180px;
}
.main-contact .col:first-child{
    width: 540px;
}
.main-contact .col:last-child{
    width: 445px;
}
.main-contact .col .col-title{
    margin: 0 0 24px;
    font-weight: 500;
    font-size: 32px;
    line-height: 41px;
}
.main-contact .col p{
    margin: 0 0 24px;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
}
.main-contact .col>p:last-child{
    margin: 0;
}
.main-contact .col blockquote{
    border-left: 3px solid #FFDF31;
    padding-left: 12px;
    margin-left: 0;
}
.main-contact .col blockquote p{
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
}
.main-contact .col a{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F5F5F5;
    border-radius: 8px;
    height: 73px;
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
}
.contact-form{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.contact-form .form-cell{
    width: 100%;
    position: relative;
    margin-bottom: 30px;
}
.contact-form .form-cell.half{
    width: calc(50% - 15px)
}

.contact-form .form-cell.captcha{
    width: auto;
}
.contact-form .form-cell.note{
    width: calc(100% - 334px);
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    opacity: 0.3;
    padding-top: 30px;
}

.input-label{
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 12px;
}
.input-label.valid::after{
    content: '';
    display: inline-block;
    width: 24px;
    height: 24px;
    font-size: 0;
    background-image: url(../../img/valid.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-left: 8px;
    vertical-align: text-bottom;
}
textarea,input{
    border: none;
    background: #F5F5F5;
    border-radius: 8px;
    display: block;
    width: 100%;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    padding: 12px 16px;
    height: 47px;
}
textarea{
    resize: none;
    height: 94px;
}
input.invalid{
    background-color: rgba(255, 102, 102, .3);
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: rgba(0, 0, 0, .3);
  }
  ::-moz-placeholder { /* Firefox 19+ */
    color: rgba(0, 0, 0, .3);
  }
  :-ms-input-placeholder { /* IE 10+ */
    color: rgba(0, 0, 0, .3);
  }
  :-moz-placeholder { /* Firefox 18- */
    color: rgba(0, 0, 0, .3);
  }

.radio-wrap{
    height: 47px;
    display: flex;
    align-items: center;
}
.radio-wrap label{
    position: relative;
    cursor: pointer;
}
.radio-wrap label+label{
    margin-left: 30px;
}
.radio-wrap input{
    position: absolute;
    visibility: hidden;
}
.radio-wrap label span{
    display: block;
    position: relative;
    padding-left: 36px;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
}
.radio-wrap label span::before{
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -12px;
    width: 24px;
    height: 24px;
    border-radius: 12px;
    background-color: #F5F5F5;
}
.radio-wrap label span::after{
    content: '';
    display: block;
    position: absolute;
    left: 7px;
    top: 50%;
    margin-top: -5px;
    width: 10px;
    height: 10px;
    border-radius: 12px;
    background-color: #000;
    opacity: 0;
}
.radio-wrap label input:checked+span::after{
    opacity: 1;
}
.contact-form .form-cell .error{
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    position: absolute;
    left: 0;
    bottom: -20px;
    color: #FF6666;
    display: none;
}
.contact-form .btn{
    width: 100%;
}
.contact-form .acceptance label input{
    position: absolute;
    visibility: hidden;
}
.contact-form .acceptance label span{
    position: relative;
    padding-left: 36px;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    display: block;
    cursor: pointer;
}
.contact-form .acceptance label span::before{
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -12px;
    height: 24px;
    width: 24px;
    background-color: #f5f5f5;
    border-radius: 8px;
    background-repeat: no-repeat;
    background-position: center;
}
.contact-form .acceptance label input:checked+span::before{
    background-image: url(../../img/checked.svg);
}
.contact-form .nice-select{
    border: none;
    background: #F5F5F5;
    border-radius: 8px;
    display: block;
    width: 100%;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    padding: 12px 16px;
    height: 47px;
}
.contact-form .nice-select:after{
    width: 24px;
    height: 24px;
    border: none;
    transform: rotate(0deg);
    transform-origin: 50% 50%;
    background-image: url(../../img/caret-down.svg);
    background-repeat: no-repeat;
    background-position: center;
    top: 12px;
    right: 11px;
    margin: 0;
}
.contact-form .nice-select.open:after{
    transform: rotate(180deg);
}
.contact-form .drop-area{
    margin-top: 10px;
    width: 100%;
    max-width: 100%;
    border: none !important;
    background: #F5F5F5;
    border-radius: 8px;
    display: block;
    width: 100%;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    padding: 12px 16px 12px 52px;
    height: 47px;
    background-image: url(../../img/paperclip.svg);
    background-repeat: no-repeat;
    background-position: 16px 12px;
}
.contact-form .drop-area svg{
    display: none;
}
.contact-form .drop-area div>span{
    font-family: 'Euclid Circular B';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    color: #000000;
    opacity: 0.3;
}
.contact-form .drop-area span span{
    text-decoration: none !important;
}
.uploader-wrap.chosed .drop-area div>span{
    display: none;
}
.uploader-wrap{
    position: relative;
}
.uploader-wrap p{
    position: absolute;
    display: inline-flex;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    padding: 12px 16px 12px 52px;
    pointer-events: none;
}

@media screen and (max-width:1199px) {
    .contact-flex{
        flex-direction: column;
    }
    .main-contact .col:first-child{
        width: 100%;
    }
    .main-contact .col:last-child{
        width: 100%;
        margin-top: 72px;
    }
}
@media screen and (max-width:767px) {
    .contact-form .form-cell.half{
        width: 100%;
    }
    .main-contact .col:last-child{
        margin-top: 48px;
    }
    .main-contact .col .col-title{
        font-size: 24px;
    }
}