.section-Jobs{

}
.section-Jobs .slider-wrap{
    position: relative;
}
.section-Jobs .sl-nav{
    right: 0;
    top: -73px;
}
.section-Jobs .slick-list{
    overflow: visible;
}
.section-Jobs .slick-slider{
    margin-right: -15px;
    margin-left: -15px;
}
.section-Jobs .item{
    background: #F5F5F5;
    border-radius: 8px;
    position: relative;
    height: 482px;
    overflow: hidden;
    padding: 24px;
    margin-right: 15px;
    margin-left: 15px;
    cursor: pointer;
}
.section-Jobs .item img{
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
}
.section-Jobs .item .title{
    display: inline-flex;
    background-color: #fff;
    padding: 12px 16px;
    border-radius: 30px;
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
    position: relative;
    z-index: 2;
}
.section-Jobs .item .description{
    position: absolute;
    left: 0;
    bottom: 0;
    top: 0;
    right: 0;
    display: flex;
    align-items: flex-end;
    text-align: left;
    background: #FFDF31;
    padding: 24px;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    opacity: 0;
    transition: opacity .5s ease;
}
.section-Jobs .item:hover .description{
    opacity: 1;
}

@media screen and (max-width:1199px) {
    .section-Jobs .slider-wrap{
        padding-right: 9%
    }
    .section-Jobs .item{
        height: 410px;
    }
    .section-Jobs .item img{
        height: 326px;
        width: auto;
    }
    .section-Jobs .sl-nav{
        display: none;
    }
}
@media screen and (max-width:767px) {
    .section-Jobs .item{
        height: 344px;
    }
    .section-Jobs .slider-wrap{
        padding-right: 0;
        width: 280px;
        margin-right: auto;
        margin-left: auto;
    }
    .section-Jobs .item img{
        height: 260px;
    }
}