.map-wrap{
    position: relative;
    height: 540px;
}
#map{
    height: 100%;
}
.map-wrap .map-nav{
    position: absolute;
    left: 24px;
    bottom: 24px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.map-wrap .map-nav .btn{
    margin-top: 8px;
}
.map-wrap .map-nav .btn.active{
    background-color: #FFDF31;
}
@media screen and (max-width:1199px) {
    .map-wrap{
        margin-right: -39px;
        margin-left: -39px;
        height: 600px;
    }
}
@media screen and (max-width:767px) {
    .map-wrap{
        margin-right: -15px;
        margin-left: -15px;
        height: 600px;
    }
    .map-wrap .map-nav{
        left: 15px;
        bottom: 32px;
    }
    .map-wrap .map-nav .btn{
        height: 43px;
    }
    .section-Map{
        margin-bottom: 0;
    }
}