.modal,
.modal-box {
  z-index: 900;
}

.modal-sandbox {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: transparent;
}

.modal {
  display: none; 
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgb(0,0,0);
  background: rgba(0,0,0,.8);
  overflow: auto;
}

.modal-box {
  position: relative;
  width: 1110px;
  max-width: 100%;
  margin: 72px auto;
  animation-name: modalbox;
  animation-duration: .4s;
  animation-timing-function: cubic-bezier(0,0,.3,1.6);
}

.modal-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    background-color: #fff;
    border-radius: 8px 8px 0 0;
    padding: 72px 95px 0;
    z-index: 2;
    transition: all .3s ease;
}
.scrolled .modal-header{
  padding: 30px 95px;
}

.modal-body {
  background: #fff;
  padding: 48px 95px 60px;
  border-radius: 0 0 8px 8px;
}
.modal-title{
  font-weight: 500;
  font-size: 72px;
  line-height: 66px;
  transition: all .3s ease;
}
.scrolled .modal-title{
  font-weight: 500;
  font-size: 24px;
  line-height: 30px;
}

/* Close Button */
.close-modal {
  text-align: right;
  cursor: pointer;
  position: relative;
  transition: transform .3s ease;
  font-size: 0;
  position: absolute;
  top: 32px;
  right: 32px;
}
.close-modal:hover{
  transform: rotate(90deg);
}
.close-modal:hover path{
  stroke:red
}

.modal-body p{
  margin: 0 0 24px;
  line-height: 22px;
  font-size: 14px;
}
.modal-body h4{
  font-size: 18px;
  margin: 48px 0 24px;
}
.modal-body h4:first-child{
  margin-top: 0;
}

/* Animation */
@-webkit-keyframes modalbox {
  0% {
    top: -250px; 
    opacity: 0;
  }
  100% {
    top: 0; 
    opacity: 1;
  }
}

@keyframes modalbox {
  0% {
    top: -250px; 
    opacity: 0;
  }
  100% {
    top: 0; 
    opacity: 1;
  }
}

@media screen and (max-width:1199px) {
  .modal-box{
    width: 684px;
  }
  .modal-body,
  .modal-header{
    padding-left: 24px;
    padding-right: 24px;
  }
  .modal-header{
    padding-top: 32px;
  }
  .scrolled .modal-header {
    padding: 30px 24px;
  }
}
@media screen and (max-width:767px) {
  .modal-box{
    width: 100%;
    margin: 0;
  }
  .modal-header{
    padding-top: 72px;
  }
  .modal-title{
    font-weight: 500;
    font-size: 48px;
    line-height: 44px;
  }
}