.header{
    background-color: #fff;
}
.header.secondary{
    background-color: #F5F5F5;
}
.header .d-flex{
    height: 111px;
}
.header .btn{
    margin-left: 32px;
}
.header-phone{
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    text-decoration: none;
    color: inherit;
}
.header-phone svg{
    display: none;
}
@media screen and (max-width:767px) {
    .header-phone{
        font-size: 0;
        line-height: 0;
    }
    .header-phone svg{
        display: inline;
    }
    .header-action{
        font-size: 0;
        line-height: 0;
    }
    .header .btn{
        position: fixed;
        z-index: 10;
        margin: 0;
        top: 15px;
        left: 15px;
        width: calc(100vw - 30px);
        transform: translateY(-150%);
    }
    .header.scrolled .btn{
        transform: translateY(0);
    }
    .header-brand img{
        width: 185px;
    }
    .header .d-flex{
        height: 78px;
    }
}