/* global box-sizing */
*,
*:after,
*:before {
	-moz-box-sizing:border-box;
	box-sizing:border-box;
	-webkit-font-smoothing:antialiased;
	font-smoothing:antialiased;
	text-rendering:optimizeLegibility;
}

/* clear */
.clear:before,
.clear:after {
    content:' ';
    display:table;
}

.clear:after {
    clear:both;
}
.clear {
    *zoom:1;
}
img {
	max-width:100%;
	vertical-align:bottom;
}
a {
	color:#444;
	text-decoration:none;
}
a:hover {
	color:#444;
}
a:focus {
	outline:0;
}
a:hover,
a:active {
	outline:0;
}
textarea:focus,
input:focus {
	outline:0;
	border:1px solid #04A4CC;
}

.app{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.app>*{
  width: 100%;
}
.container{
  width: 1140px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  max-width: 100%;
}
.d-flex{
  display: flex;
}
.j-between{
  justify-content: space-between;
}
.a-center{
  align-items: center;
}
.f-wrap{
  flex-wrap: wrap;
}

.btn{
  text-decoration: none;
  border: none;
  background: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0 16px;
  height: 47px;
  font-weight: 500;
  font-size: 18px;
  border-radius: 8px;
  color: inherit;
  transition: all .3s ease;
}
.btn.btn-fill{
  background-color: #f5f5f5;
  color: #000;
}
.btn.btn-fill.white:hover,
.btn.btn-fill:hover{
  background-color: #FFDF31;
}
.btn.btn-fill.white{
  background-color: #fff;
}
.btn.btn-fill.yellow{
  background-color: #FFDF31;
  color: #000;
}
.btn.btn-fill.yellow:hover{
  background-color: #ff7b4b;
}
.section{
  margin-bottom: 72px;
  overflow: hidden;
}
.section-title{
  font-weight: 500;
  font-size: 32px;
  line-height: 41px;
  margin: 0 0 32px;
}

.page-title{
  font-weight: 500;
  font-size: 72px;
  line-height: 66px;
  margin: 72px 0 50px;
}

@media screen and (max-width:1199px) {
  .container{
    width: 100%;
    padding: 0 39px;
  }
}
@media screen and (max-width:767px) {
  .container{
    width: 100%;
    padding: 0 15px;
  }
  .section{
    margin-bottom: 47px;
  }
  .section-title{
    font-size: 24px;
  }
  .page-title{
    font-size: 48px;
    line-height: 44px;
    margin: 48px 0;
  }
}