.footer{
    background: #F5F5F5;
    padding-top: 72px;
    padding-bottom: 32px;
    margin-top: auto;
}
.footer-top{
    background: #FFFFFF;
    border-radius: 8px;
    padding: 24px;
    margin-bottom: 24px;
}
.footer-social{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 50%;
}
.footer-social p{
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 41px;
    margin: 0;
    text-transform: lowercase;
}
.footer-social ul{
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.footer-social ul li{
    margin-left: 8px;
    font-size: 0;
}
.footer-copyright .col{
    width: 50%;
}
.footer-copyright p{
    margin: 0;
    font-weight: 500;
}
.footer-copyright a{
    font-weight: 500;
    text-decoration: none;
    color: inherit;
}
@media screen and (max-width:767px) {
    .footer{
        padding-top: 48px;
    }
    .footer-copyright{
        flex-direction: column-reverse;
    }
    .footer-copyright .col{
        width: 100%;
        margin-top: 24px;
    }
    .footer-top{
        margin-bottom: 0;
        margin-right: -15px;
        margin-left: -15px;
        padding: 24px 15px;
    }
    .footer-social p{
        display: none;
    }
    .footer-top>.d-flex{
        flex-direction: column;
        align-items: flex-start;
    }
    .footer-social{
        margin-top: 24px;
    }
    .footer-social ul li{
        margin-left: 0;
        margin-right: 8px;
    }
    .footer-brand img{
        width: 185px;
    }
}