.gallery-wrap #listPhotos{
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
}
.gallery-wrap #listPhotos a{
    position: relative;
    background-color: #F5F5F5;
    border-radius: 8px;
    aspect-ratio: 1;
}
.gallery-wrap #listPhotos a:first-child{
    grid-column: 1 / 3;
    grid-row: 1 / 3;   
}
.gallery-wrap #listPhotos a:nth-child(1n+6){
    display: none;
}
.gallery-wrap #listPhotos a img{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: none;
}
.gallery-wrap .show-more{
    width: 100%;
    margin-top: 30px;
}
@media screen and (max-width:1199px) {
    .gallery-wrap #listPhotos{
        display: flex;
        flex-wrap: wrap;
    }
    .gallery-wrap #listPhotos a{
        width: calc(100%/3 - 20px);
    }
    .gallery-wrap #listPhotos a:nth-child(2),
    .gallery-wrap #listPhotos a:first-child{
        width: calc(50% - 15px);
    }
}
@media screen and (max-width:767px) {
    .gallery-wrap #listPhotos{
        gap: 12px;
    }
    .gallery-wrap #listPhotos a,
    .gallery-wrap #listPhotos a:nth-child(2),
    .gallery-wrap #listPhotos a:first-child{
        width: 100%
    }
    .gallery-wrap .show-more{
        margin-top: 12px;
    }
}