.section-MainSlider{
    background-color: #F5F5F5;
}
.section-MainSlider .slider-wrap{
    position: relative;
}
.sl-nav{
    display: flex;
    justify-content: space-between;
    position: absolute;
    background: #FFFFFF;
    border-radius: 8px;
    display: flex;
    padding: 10px 8px;
    width: 88px;
}
.section-MainSlider .sl-nav{
    bottom: 48px;
    left: 0;
}
.sl-nav button{
    padding: 0;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 0;
}
.sl-nav button:hover path{
    opacity: 1;
}
.section-MainSlider .sl-nav .next{

}
.section-MainSlider .sl-nav .prev{

}

.section-MainSlider .sl-nav:before{
    content: '';
    display: block;
    position: absolute;
    background: #F5F5F5;
    width: 1px;
    left: 50%;
    top: 8px;
    height: 28px;
    transform: translateX(-50%);
}
.section-MainSlider .item .thumb{
    width: 615px;
    height: 615px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    align-items: center;
    font-size: 0;
}
.section-MainSlider .item .thumb img{
    position: relative;
    width: 100%;
    height: 400px;
    object-fit: cover;
}
.section-MainSlider .item .d-flex{
    justify-content: space-between;
    height: 400px;
    overflow: hidden;
}
.section-MainSlider .item .d-flex .col:last-child{
    align-self: center;
    position: relative;
    height: 400px;
    display: flex;
    align-items: center;
}
.section-MainSlider .item .text{
    position: absolute;
    display: inline-flex;
    padding: 12px 16px;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 30px;
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
}

#spa-1 .text-1{
    top: 48px;
    right: 120px;
}
#spa-1 .text-2{
    bottom: 48px;
    left: 120px;
}
#spa-2 .text-1{
    top: 48px;
    left: 120px;
}
#spa-2 .text-2{
    bottom: 48px;
    right: 120px;
}

.section-MainSlider .item .title{
    padding-top: 48px;
    font-weight: 500;
    font-size: 72px;
    line-height: 66px;
}

@media screen and (max-width: 1199px) {
    .section-MainSlider .item .d-flex{
        position: relative;
    }
    .section-MainSlider .item .d-flex .col:last-child{
        position: absolute;
        right: -245px;
    }
    .section-MainSlider .slider-wrap{
        margin-right: -39px;
    }
    .section-MainSlider .item .thumb img{
        object-position: -120px 0;
    }
    #spa-1 .text-2,
    #spa-2 .text-1{
        left: 83px;
    }
    #spa-1 .text-1,
    #spa-2 .text-2{
        right: auto;
        left: 215px;
    }
    .section-MainSlider .item#spa-2 .title{
        font-size: 64px;
    }
}
@media screen and (max-width: 767px){
    .section-MainSlider .item .d-flex{
        height: 590px;
    }
    .section-MainSlider .item#spa-2 .title,
    .section-MainSlider .item .title{
        font-size: 48px;
        padding-right: 15px;
        padding-left: 15px;
        line-height: 44px;
    }
    .section-MainSlider .sl-nav,
    .section-MainSlider .item .title br{display: none;}

    .section-MainSlider .slider-wrap{
        margin-right: -15px;
        margin-left: -15px;
    }
    .section-MainSlider .item .d-flex .col:last-child{
        bottom: 0;
        left: 0;
        right: auto;
        height: 407px;
        width: 100%;
    }
    .section-MainSlider .item .thumb img{
        height: 407px;
        position: absolute;
        top: 0;
    }
    .section-MainSlider .item .thumb img {
        object-position: -56px 0;
    }
    .section-MainSlider .item .thumb{
        width: 539px;
        height: 539px;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
    }
    #spa-1 .text-2,
    #spa-2 .text-1{
        left: 15px;
    }
    #spa-1 .text-1,
    #spa-2 .text-2{
        right: 15px;
        left: auto;
    }
    #spa-2 .text-1,
    #spa-1 .text-1{
        top: 96px;
    }
    #spa-2 .text-2,
    #spa-1 .text-2{
        bottom: 90px;
    }
}